button.button { /*Todos los botones que tengan la clase de button*/
    transition: all 0.5 ease;
    background-color: #2185D0;
    border-radius: 4px;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-weight: 700;
    font-size: 15px;
    cursor: pointer;
}

button.button:hover {
    background-color: #1678c2;
}